$backgroundColor: #f5f5f5;

header {
  background: darken($backgroundColor, 75);
  border-bottom: 1px solid #eee;
  color: #eee;
  padding: 0;
  margin: 0;
  text-align: center;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

body {
  background: $backgroundColor;
  height: 100%;

  #root {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  footer {
    background: darken($backgroundColor, 75);
    border-top: 1px solid #eee;
    color: #eee;
    padding: 0.5em 3em;
    margin: 0;
    text-align: center;
  }
}
